import { reactive, ref, onBeforeMount } from "vue";
import $store from '../../store/index.js';
export default {
    name: "Login",
    setup: function (props) {
        var formData = reactive({
            phone: null,
            code: null,
        });
        var safeArea = ref(0);
        var validFromData = function (data) {
            var isTrue = true;
            return new Promise(function (res, reg) {
                for (var k in data) {
                    if (!data[k]) {
                        if (k === 'phone') {
                            uni.showToast({
                                icon: 'none',
                                title: "请填写手机号~"
                            });
                        }
                        else {
                            uni.showToast({
                                icon: 'none',
                                title: "验证码不能为空~"
                            });
                        }
                        isTrue = false;
                        break;
                    }
                }
                if (isTrue) {
                    res(data);
                }
            });
        };
        var submit = function () {
            validFromData(formData).then(function (res) {
                console.log(res);
            });
        };
        onBeforeMount(function () {
            safeArea.value = $store.state.system.safeArea.height + 'px';
        });
        return {
            formData: formData,
            safeArea: safeArea,
            validFromData: validFromData,
            submit: submit,
            props: props
        };
    }
};
